<template>
    <div>
        <div>
            <div style="display:flex;justify-content:space-between;">
                <div>
                    <el-input placeholder="请输入员工名搜索，可以直接回车..." prefix-icon="el-icon-search"
                              clearable
                              :disabled="showAdvSearchView"
                              @clear="initEmps"
                              v-model="keywords"
                              @keydown.enter.native="initEmps" style="width:400px;margin-right:10px;"></el-input>
                    <el-button icon="el-icon-search" type="primary" @click="initEmps" :disabled="showAdvSearchView">搜索</el-button>
                    <el-button type="primary" @click="showAdvSearchView=!showAdvSearchView"><i :class="showAdvSearchView?'fa fa-angle-double-up':'fa fa-angle-double-down'" aria-hidden="true"></i>高级搜索</el-button>
                </div>
                <div>
                    <el-upload
                            :show-file-list="false"
                            :on-success="onSuccess"
                            :on-error="onError"
                            :disabled="disabledImport"
                            :before-upload="beforeUpload"
                            style="display: inline-flex;margin-right: 10px;"
                            action="/employee/basic/import/">
                        <el-button :disabled="disabledImport" type="success" :icon="importIcon">{{importData}}</el-button>
                    </el-upload>

                    <el-button type="success" @click="exportData" icon="el-icon-download">导出数据</el-button>
                    <el-button type="primary" icon="el-icon-plus" @click="showTip">添加用户</el-button>
                </div>
            </div>
            <transition name="fade">
            <div v-show="showAdvSearchView" style="border: 1px solid #409eff;border-radius: 5px;box-sizing: border-box;padding: 5px 0px;margin: 10px 0px;">
                <el-row>
                    <el-col :span="5">
                        政治面貌：
                        <el-select v-model="empAdv.politicid" placeholder="请选择" size="mini"
                                   style="width: 130px;">
                            <el-option
                                    v-for="(po,indexPo) in politis"
                                    :key="indexPo"
                                    :label="po.name"
                                    :value="po.id">
                            </el-option>
                        </el-select>
                    </el-col>
                    <el-col :span="4">
                        民族：
                        <el-select v-model="empAdv.nationid" placeholder="请选择" size="mini"
                                   style="width: 130px;">
                            <el-option
                                    v-for="(n,indexN) in nations"
                                    :key="indexN"
                                    :label="n.name"
                                    :value="n.id">
                            </el-option>
                        </el-select>
                    </el-col>
                    <el-col :span="4">
                        职位：
                        <el-select v-model="empAdv.posid" placeholder="请选择" size="mini" style="width: 130px;">
                            <el-option
                                    v-for="(ps,indexPs) in poss"
                                    :key="indexPs"
                                    :label="ps.name"
                                    :value="ps.id">
                            </el-option>
                        </el-select>
                    </el-col>
                    <el-col :span="4">
                        职称：
                        <el-select v-model="empAdv.joblevelid" placeholder="请选择" size="mini"
                                   style="width: 130px;">
                            <el-option
                                    v-for="(jb,indexJb) in jobs"
                                    :key="indexJb"
                                    :label="jb.name"
                                    :value="jb.id">
                            </el-option>
                        </el-select>
                    </el-col>
                    <el-col :span="7">
                        聘用形式：
                        <el-radio-group v-model="empAdv.engageform">
                            <el-radio label="劳动合同">劳动合同</el-radio>
                            <el-radio label="劳务合同">劳务合同</el-radio>
                        </el-radio-group>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="5">
                        所属部门：
                        <el-popover
                                placement="right"
                                title="请选择部门"
                                width="200"
                                trigger="manual"
                                v-model="visible1">
                            <el-tree
                                    :data="allDepData"
                                    :props="defaultProps"
                                    default-expand-all
                                    @node-click="advHandleClick">
                            </el-tree>
                            <div slot="reference" style="width: 130px;display: inline-flex;font-size: 13px;border: 1px solid #dedede;height: 26px;border-radius: 5px;
                                    cursor: pointer;align-items: center;padding-left: 10px;margin-top:3px;box-sizing: border-box"
                                 @click="AdvShowDepmt">{{selectDep}}
                            </div>
                        </el-popover>
                    </el-col>
                    <el-col :span="10">
                        入职日期：
                        <el-date-picker
                                v-model="empAdv.beDate"
                                type="daterange"
                                size="mini"
                                unlink-panels
                                value-format="yyyy-MM-dd"
                                range-separator="至"
                                start-placeholder="开始日期"
                                end-placeholder="结束日期">
                        </el-date-picker>
                    </el-col>
                    <el-col :span="5" :offset="4">
                        <el-button  @click="cancleAdv">取消</el-button>
                        <el-button icon="el-icon-search" @click="initEmps" type="primary">搜索</el-button>
                    </el-col>
                </el-row>
            </div>
            </transition>

        </div>
        <div>
            <el-table
                    :data="empData"
                    border
                    style="width: 100%">
                <el-table-column
                        type="selection"
                        width="55">
                </el-table-column>
                <el-table-column
                        prop="name"
                        label="姓名"
                        align="left"
                        fixed
                        width="90">
                </el-table-column>
                <el-table-column
                        prop="workid"
                        label="工号"
                        width="90">
                </el-table-column>
                <el-table-column
                        prop="gender"
                        label="性别"
                        width="90">
                </el-table-column>
                <el-table-column
                        prop="birthday"
                        label="出生日期"
                        width="140">
                </el-table-column>
                <el-table-column
                        prop="idcard"
                        label="身份证号码"
                        width="150">
                </el-table-column>
                <el-table-column
                        prop="wedlock"
                        label="婚姻状况"
                        width="90">
                </el-table-column>
                <el-table-column
                        prop="nation.name"
                        label="民族"
                        width="60">
                </el-table-column>
                <el-table-column
                        prop="nativeplace"
                        label="籍贯"
                        width="60">
                </el-table-column>
                <el-table-column
                        prop="politicsstatus.name"
                        label="政治面貌"
                        width="90">
                </el-table-column>
                <el-table-column
                        prop="email"
                        label="电子邮件"
                        width="150">
                </el-table-column>
                <el-table-column
                        prop="phone"
                        label="电话号码"
                        width="120">
                </el-table-column>
                <el-table-column
                        prop="address"
                        label="联系地址"
                        width="140">
                </el-table-column>
                <el-table-column
                        prop="department.name"
                        label="所属部门"
                        width="90">
                </el-table-column>
                <el-table-column
                        prop="position.name"
                        label="职位"
                        width="90">
                </el-table-column>
                <el-table-column
                        prop="jobLevel.name"
                        label="职称"
                        width="90">
                </el-table-column>
                <el-table-column
                        prop="engageform"
                        label="聘用形式"
                        width="90">
                </el-table-column>
                <el-table-column
                        prop="tiptopdegree"
                        label="最高学历"
                        width="90">
                </el-table-column>
                <el-table-column
                        prop="specialty"
                        label="专业"
                        width="150">
                </el-table-column>
                <el-table-column
                        prop="school"
                        label="毕业院校"
                        width="150">
                </el-table-column>
                <el-table-column
                        prop="begindate"
                        label="入职日期"
                        width="140">
                </el-table-column>
                <el-table-column
                        prop="conversiontime"
                        label="转正日期"
                        width="140">
                </el-table-column>
                <el-table-column
                        prop="begincontract"
                        label="合同起始日期"
                        width="140">
                </el-table-column>
                <el-table-column
                        prop="endcontract"
                        label="合同结束日期"
                        width="140">
                </el-table-column>
                <el-table-column
                        label="合同期限"
                        width="90">
                    <template slot-scope="scope">
                        <el-tag>{{scope.row.contractterm}}年</el-tag>
                    </template>
                </el-table-column>
                <el-table-column
                        label="操作"
                        fixed="right"
                        width="260">
                    <template slot-scope="scope">
                        <el-button @click="showEdit(scope.row)" size="mini">编辑</el-button>
                        <el-button size="mini" type="primary">查看高级资料</el-button>
                        <el-button @click="deleteEmp(scope.row)" size="mini" type="danger">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <div style="display: flex;justify-content: flex-end">
            <el-pagination
                    background
                    @size-change="sizeChange"
                    @current-change="currentChange"
                    layout="sizes, prev, pager, next, jumper, ->, total, slot"
                    :total="total">
            </el-pagination>
        </div>
        <div>
            <el-dialog
                    :title="title"
                    :visible.sync="dialogVisible"
                    width="80%">
                <div>
                    <el-form :model="emp" :rules="rules" ref="empForm">
                        <el-row>
                            <el-col :span="6">
                                <el-form-item label="姓名：" prop="name">
                                    <el-input size="mini" style="width:150px" prefix-icon="el-icon-edit"
                                              v-model="emp.name" placeholder="请输入员工姓名"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="5">
                                <el-form-item label="性别：" prop="gender">
                                    <el-radio-group v-model="emp.gender">
                                        <el-radio label="男">男</el-radio>
                                        <el-radio label="女">女</el-radio>
                                    </el-radio-group>
                                </el-form-item>
                            </el-col>
                            <el-col :span="6">
                                <el-form-item label="出生日期：" prop="birthday">
                                    <el-date-picker
                                            v-model="emp.birthday"
                                            size="mini"
                                            type="date"
                                            value-format="yyyy-MM-dd"
                                            style="width: 150px;"
                                            placeholder="选择日期">
                                    </el-date-picker>
                                </el-form-item>
                            </el-col>
                            <el-col :span="7">
                                <el-form-item label="政治面貌：" prop="politicid">
                                    <el-select v-model="emp.politicid" placeholder="请选择" size="mini"
                                               style="width: 200px;">
                                        <el-option
                                                v-for="(po,indexPo) in politis"
                                                :key="indexPo"
                                                :label="po.name"
                                                :value="po.id">
                                        </el-option>
                                    </el-select>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="6">
                                <el-form-item label="民族：" prop="nationid">
                                    <el-select v-model="emp.nationid" placeholder="请选择" size="mini"
                                               style="width: 150px;">
                                        <el-option
                                                v-for="(n,indexN) in nations"
                                                :key="indexN"
                                                :label="n.name"
                                                :value="n.id">
                                        </el-option>
                                    </el-select>
                                </el-form-item>
                            </el-col>
                            <el-col :span="5">
                                <el-form-item label="籍贯：" prop="nativeplace">
                                    <el-input size="mini" style="width: 150px;" prefix-icon="el-icon-edit"
                                              v-model="emp.nativeplace" placeholder="请输入籍贯"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="6">
                                <el-form-item label="电子邮箱：" prop="email">
                                    <el-input size="mini" style="width: 150px;" prefix-icon="el-icon-message"
                                              v-model="emp.email" placeholder="请输入电子邮箱"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="7">
                                <el-form-item label="联系地址：" prop="address">
                                    <el-input size="mini" style="width: 200px;" prefix-icon="el-icon-edit"
                                              v-model="emp.address" placeholder="请输入联系地址"></el-input>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="6">
                                <el-form-item label="职位：" prop="posid">
                                    <el-select v-model="emp.posid" placeholder="请选择" size="mini" style="width: 150px;">
                                        <el-option
                                                v-for="(ps,indexPs) in poss"
                                                :key="indexPs"
                                                :label="ps.name"
                                                :value="ps.id">
                                        </el-option>
                                    </el-select>
                                </el-form-item>
                            </el-col>
                            <el-col :span="5">
                                <el-form-item label="职称：" prop="joblevelid">
                                    <el-select v-model="emp.joblevelid" placeholder="请选择" size="mini"
                                               style="width: 150px;">
                                        <el-option
                                                v-for="(jb,indexJb) in jobs"
                                                :key="indexJb"
                                                :label="jb.name"
                                                :value="jb.id">
                                        </el-option>
                                    </el-select>
                                </el-form-item>
                            </el-col>
                            <el-col :span="6">
                                <el-form-item label="所属部门：" prop="departmentid">
                                    <el-popover
                                            placement="right"
                                            title="请选择部门"
                                            width="200"
                                            trigger="manual"
                                            v-model="visible">
                                        <el-tree
                                                :data="allDepData"
                                                :props="defaultProps"
                                                default-expand-all
                                                @node-click="handleClick">
                                        </el-tree>
                                        <div slot="reference" style="width: 150px;display: inline-flex;font-size: 13px;border: 1px solid #dedede;height: 26px;border-radius: 5px;
                                    cursor: pointer;align-items: center;padding-left: 10px;box-sizing: border-box"
                                             @click="showDepmt">{{selectDep}}
                                        </div>
                                    </el-popover>

                                </el-form-item>
                            </el-col>
                            <el-col :span="7">
                                <el-form-item label="电话号码：" prop="phone">
                                    <el-input size="mini" style="width: 200px;" prefix-icon="el-icon-phone"
                                              v-model="emp.phone" placeholder="请输入电话号码"></el-input>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="6">
                                <el-form-item label="工号：" prop="workid">
                                    <el-input size="mini" style="width: 150px;" prefix-icon="el-icon-message"
                                              v-model="emp.workid" disabled placeholder="请输入工号"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="5">
                                <el-form-item label="学历：" prop="tiptopdegree">
                                    <el-select v-model="emp.tiptopdegree" placeholder="请选择" size="mini"
                                               style="width: 150px;">
                                        <el-option
                                                v-for="item in tiptopdegrees"
                                                :key="item"
                                                :label="item"
                                                :value="item">
                                        </el-option>
                                    </el-select>
                                </el-form-item>
                            </el-col>
                            <el-col :span="6">
                                <el-form-item label="毕业院校：" prop="school">
                                    <el-input size="mini" style="width: 150px;" prefix-icon="el-icon-edit"
                                              v-model="emp.school" placeholder="请输入毕业院校"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="7">
                                <el-form-item label="专业名称：" prop="specialty">
                                    <el-input size="mini" style="width: 200px;" prefix-icon="el-icon-edit"
                                              v-model="emp.specialty" placeholder="请输入专业名称"></el-input>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="6">
                                <el-form-item label="入职日期：" prop="begindate">
                                    <el-date-picker
                                            v-model="emp.begindate"
                                            size="mini"
                                            type="date"
                                            value-format="yyyy-MM-dd hh:mm:ss"
                                            style="width: 130px;"
                                            placeholder="选择日期">
                                    </el-date-picker>
                                </el-form-item>
                            </el-col>
                            <el-col :span="5">
                                <el-form-item label="转正日期：" prop="conversiontime">
                                    <el-date-picker
                                            v-model="emp.conversiontime"
                                            size="mini"
                                            type="date"
                                            value-format="yyyy-MM-dd hh:mm:ss"
                                            style="width: 130px;"
                                            placeholder="选择日期">
                                    </el-date-picker>
                                </el-form-item>
                            </el-col>
                            <el-col :span="6">
                                <el-form-item label="合同起始日期：" prop="begincontract">
                                    <el-date-picker
                                            v-model="emp.begincontract"
                                            size="mini"
                                            type="date"
                                            value-format="yyyy-MM-dd hh:mm:ss"
                                            style="width: 130px;"
                                            placeholder="选择日期">
                                    </el-date-picker>
                                </el-form-item>
                            </el-col>
                            <el-col :span="7">
                                <el-form-item label="合同终止日期：" prop="endcontract">
                                    <el-date-picker
                                            v-model="emp.endcontract"
                                            size="mini"
                                            type="date"
                                            value-format="yyyy-MM-dd hh:mm:ss"
                                            style="width: 172px;"
                                            placeholder="选择日期">
                                    </el-date-picker>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="8">
                                <el-form-item label="身份证号：" prop="idcard">
                                    <el-input size="mini" style="width: 150px;" prefix-icon="el-icon-edit"
                                              v-model="emp.idcard" placeholder="请输入身份证号"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="8">
                                <el-form-item label="聘用形式：" prop="engageform">
                                    <el-radio-group v-model="emp.engageform">
                                        <el-radio label="劳动合同">劳动合同</el-radio>
                                        <el-radio label="劳务合同">劳务合同</el-radio>
                                    </el-radio-group>
                                </el-form-item>
                            </el-col>
                            <el-col :span="8">
                                <el-form-item label="婚姻状况：" prop="wedlock">
                                    <el-radio-group v-model="emp.wedlock">
                                        <el-radio label="已婚">已婚</el-radio>
                                        <el-radio label="未婚">未婚</el-radio>
                                        <el-radio label="离异">离异</el-radio>
                                    </el-radio-group>
                                </el-form-item>
                            </el-col>
                        </el-row>
                    </el-form>
                </div>
                <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="doAdd">确 定</el-button>
  </span>
            </el-dialog>
        </div>
    </div>
</template>

<script>
    export default {
        name: "EmpBasic",
        data() {
            return {
                title: '',
                importIcon:'el-icon-upload2',
                importData:'导入数据',
                showAdvSearchView:false,
                disabledImport:false,
                empData: [],
                allDepData: [],
                visible: false,
                visible1: false,
                dialogVisible: false,
                poss: [],
                politis: [],
                nations: [],
                jobs: [],
                tiptopdegrees: ['本科', '大专', '硕士', '博士', '高中', '初中', '小学', '其他'],
                keywords: '',
                selectDep: '',
                total: 0,
                page: 1,
                size: 10,
                empAdv:{
                    politicid:null,
                    nationid:null,
                    posid:null,
                    joblevelid:null,
                    engageform:null,
                    departmentid:null,
                    beDate:null

                },
                emp: {
                    name: "",
                    gender: "",
                    birthday: "",
                    idcard: "",
                    wedlock: "",
                    nationid: null,
                    nativeplace: "",
                    politicid: null,
                    email: "",
                    phone: "",
                    address: "",
                    departmentid: null,
                    joblevelid: null,
                    posid: null,
                    engageform: "",
                    tiptopdegree: "",
                    specialty: "",
                    school: "",
                    begindate: "",
                    workid: "",
                    contractterm: null,
                    conversiontime: "",
                    notworkdate: null,
                    begincontract: "",
                    endcontract: "",
                    workage: null
                },
                defaultProps: {
                    children: 'children',
                    label: 'name'
                },
                rules: {
                    name: [{required: true, message: '请输入用户名', trigger: 'blur'}],
                    gender: [{required: true, message: '请输入性别', trigger: 'blur'}],
                    birthday: [{required: true, message: '请输入出生日期', trigger: 'blur'}],
                    idcard: [{required: true, message: '请输入身份证号', trigger: 'blur'}, {
                        pattern: /^[1-9][0-9]{5}(19|20)[0-9]{2}((01|03|05|07|08|10|12)(0[1-9]|[1-2][0-9]|31)|(04|06|09|11)(0[1-9]|[1-2][0-9]|30)|02(0[1-9]|[1-2][0-9]))[0-9]{3}([0-9]|x|X)$/,
                        message: '身份证号码格式不正确',
                        trigger: 'blur'
                    }],
                    wedlock: [{required: true, message: '请输入婚姻状况', trigger: 'blur'}],
                    nationid: [{required: true, message: '请输入民族', trigger: 'blur'}],
                    nativeplace: [{required: true, message: '请输入籍贯', trigger: 'blur'}],
                    politicid: [{required: true, message: '请输入政治面貌', trigger: 'blur'}],
                    email: [{required: true, message: '请输入电子邮箱', trigger: 'blur'}, {
                        type: 'email',
                        message: '邮箱格式不正确',
                        trigger: 'blur'
                    }],
                    phone: [{required: true, message: '请输入电话号码', trigger: 'blur'}],
                    address: [{required: true, message: '请输入联系地址', trigger: 'blur'}],
                    departmentid: [{required: true, message: '请输入所属部门', trigger: 'blur'}],
                    joblevelid: [{required: true, message: '请输入职位', trigger: 'blur'}],
                    posid: [{required: true, message: '请输入职称', trigger: 'blur'}],
                    engageform: [{required: true, message: '请输入聘用形式', trigger: 'blur'}],
                    tiptopdegree: [{required: true, message: '请输入最高学历', trigger: 'blur'}],
                    specialty: [{required: true, message: '请输入专业名称', trigger: 'blur'}],
                    school: [{required: true, message: '请输入毕业院校', trigger: 'blur'}],
                    begindate: [{required: true, message: '请输入入职日期', trigger: 'blur'}],
                    contractterm: [{required: true, message: '请输入合同期限', trigger: 'blur'}],
                    conversiontime: [{required: true, message: '请输入转正日期', trigger: 'blur'}],
                    notworkdate: [{required: true, message: '请输入用户名', trigger: 'blur'}],
                    begincontract: [{required: true, message: '请输入合同起始日期', trigger: 'blur'}],
                    endcontract: [{required: true, message: '请输入合同结束日期', trigger: 'blur'}],
                    // workage:[{required:true,message:'请输入用户名',trigger:'blur'}]
                },
                str2date:[]
            }
        },
        mounted() {
            this.initEmps();
            this.initNation();
            this.initJobs();
            this.initPoss();
            this.initPolitis();
            this.initDepmts();
        },
        methods: {


            cancleAdv() {
                this.empAdv = {
                    politicid: null,
                    nationid: null,
                    posid: null,
                    joblevelid: null,
                    engageform: null,
                    departmentid: null,
                    beDate: null

                }
            },
            onError(err, file, fileList) {
                this.importIcon = "el-icon-upload2";
                this.importData = "导入数据";
                his.disabledImport = false;
            },
            onSuccess(resp, file, fileList) {
                this.importIcon = "el-icon-upload2";
                this.importData = "导入数据";
                this.disabledImport = false;
                this.initEmps();
            },
            beforeUpload() {
                this.importIcon = "el-icon-loading";
                this.importData = "正在导入...";
                this.disabledImport = true;
            },
            exportData() {
                window.open("/employee/basic/export/", "_parent");
            },
            showEdit(data) {
                this.title = "编辑员工信息";
                this.emp = data;
                this.selectDep = data.department.name;
                this.dialogVisible = true;

            },
            deleteEmp(data) {
                this.$confirm('此操作将永久删除【' + data.name + '】个数据, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.deleteRequest("/employee/basic/" + data.id).then(resp => {
                        if (resp) {
                            this.initEmps();
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            doAdd() {
                if (this.emp.id != null) {
                    this.$refs['empForm'].validate(valid => {
                        if (valid) {
                            this.putRequest("/employee/basic/", this.emp).then(resp => {
                                if (resp) {
                                    this.initEmps();
                                    this.dialogVisible = false;
                                }
                            })
                        }
                    })
                } else {
                    this.$refs['empForm'].validate(valid => {
                        if (valid) {
                            this.postRequest("/employee/basic/", this.emp).then(resp => {
                                if (resp) {
                                    this.initEmps();
                                    this.dialogVisible = false;
                                }
                            })
                        }
                    })
                }
            },
            advHandleClick(data) {
                this.selectDep = data.name;
                this.empAdv.departmentid = data.id;

            },
            handleClick(data) {
                this.selectDep = data.name;
                this.emp.departmentid = data.id;
            },
            AdvShowDepmt() {
                this.visible1 = !this.visible1;
            },
            showDepmt() {
                this.visible = !this.visible;
            },
            initDepmts() {
                if (!window.sessionStorage.getItem("allDepData")) {
                    this.getRequest("/employee/basic/dept/").then(resp => {
                        if (resp) {
                            this.allDepData = resp;
                            window.sessionStorage.setItem("allDepData", JSON.parse(resp));
                        }
                    })
                } else {
                    this.allDepData = JSON.parse(window.sessionStorage.getItem("allDepData"));
                }
            },
            sizeChange(sizeChange) {
                this.size = sizeChange;
                this.initEmps();
            },
            currentChange(currentChange) {
                this.page = currentChange;
                this.initEmps();
            },
            initMaxId() {
                this.postRequest("/employee/basic/maxid/").then(resp => {
                    if (resp) {
                        this.emp.workid = resp.obj;
                    }
                })
            },
            initPoss() {
                if (!window.sessionStorage.getItem("politis")) {
                    this.getRequest("/employee/basic/po/").then(resp => {
                        if (resp) {
                            this.politis = resp;
                            window.sessionStorage.setItem("politis", JSON.parse(resp));
                        }
                    })
                } else {
                    this.politis = JSON.parse(window.sessionStorage.getItem("politis"));
                }

            },
            initJobs() {
                if (!window.sessionStorage.getItem("jobs")) {
                    this.getRequest("/employee/basic/job/").then(resp => {
                        if (resp) {
                            this.jobs = resp;
                            window.sessionStorage.setItem("jobs", JSON.parse(resp));
                        }
                    })
                } else {
                    this.jobs = JSON.parse(window.sessionStorage.getItem("jobs"));
                }

            },
            initPolitis() {
                if (!window.sessionStorage.getItem("poss")) {
                    this.getRequest("/employee/basic/pos/").then(resp => {
                        if (resp) {
                            this.poss = resp;
                            window.sessionStorage.setItem("poss", JSON.parse(resp));
                        }
                    })
                } else {
                    this.poss = JSON.parse(window.sessionStorage.getItem("poss"));
                }

            },
            initNation() {
                if (!window.sessionStorage.getItem("nations")) {
                    this.getRequest("/employee/basic/nation/").then(resp => {
                        if (resp) {
                            this.nations = resp;
                            window.sessionStorage.setItem("nations", JSON.parse(resp));
                        }
                    })
                } else {
                    this.nations = JSON.parse(window.sessionStorage.getItem("nations"));
                }

            },
            showTip() {
                this.title = "添加员工";
                this.initMaxId();
                this.empClean();
                this.dialogVisible = true;
            },
            empClean() {
                this.emp = {
                    name: "",
                    gender: "",
                    birthday: "",
                    idcard: "",
                    wedlock: "",
                    nationid: null,
                    nativeplace: "",
                    politicid: null,
                    email: "",
                    phone: "",
                    address: "",
                    departmentid: null,
                    joblevelid: null,
                    posid: null,
                    engageform: "",
                    tiptopdegree: "",
                    specialty: "",
                    school: "",
                    begindate: "",
                    workid: "",
                    contractterm: null,
                    conversiontime: "",
                    notworkdate: null,
                    begincontract: "",
                    endcontract: "",
                    workage: null
                };
                this.selectDep = "";
            },
            initEmps() {
                let url = '/employee/basic/?page=' + this.page + "&size=" + this.size;
                if (this.showAdvSearchView) {
                    //showAdvSearchView==>true//高级搜索
                    if (this.empAdv.politicid) {
                        url += "&politicid=" + this.empAdv.politicid
                    }
                    if (this.empAdv.nationid) {
                        url += "&nationid=" + this.empAdv.nationid
                    }
                    if (this.empAdv.posid) {
                        url += "&posid=" + this.empAdv.posid
                    }
                    if (this.empAdv.joblevelid) {
                        url += "&joblevelid=" + this.empAdv.joblevelid
                    }
                    if (this.empAdv.engageform) {
                        url += "&engageform=" + this.empAdv.engageform
                    }
                    if (this.empAdv.departmentid) {
                        url += "&departmentid=" + this.empAdv.departmentid
                    }
                    if (this.empAdv.beDate) {
                        url += "&beDate=" + this.empAdv.beDate
                    }
                } else if (this.keywords) {
                    url += "&name=" + this.keywords;
                }
                this.getRequest(url).then(resp => {
                    if (resp) {
                        this.empData = resp.data;
                        this.total = resp.tatol;
                    }
                })
            }
        }
    }
</script>

<style>
    .fade-enter-active, .fade-leave-active {
        transition: opacity .8s;
    }
    .fade-enter, .fade-leave-to  {
        opacity: 0;
    }

</style>